/* Structure ============================================= */
:root {   
    --text-black: #020202;
}
.contentWrapper {
    padding-left: 32px;
    padding-right: 32px;
}
.namingWrapper {
    background: linear-gradient(316deg, #D2F1FF 15.57%, #EAF9FF 54.49%, #FFF4DE 92.04%, #FFE0CA 114.33%);
    background-blend-mode: multiply;
    position: relative;
    z-index: 4;
}
.pageTitle {
    color: var(--text-black);
    font-family: "Noe Display";
    font-size: 100px;
    font-style: normal;
    font-weight: 500;
    line-height: 90%;
    letter-spacing: -1px;
    text-align: left;
    margin: 15vh 0 0;
    max-width: 1135px;
}

/* Hero ========================================== */
.heroNaming {
    width: 100vw;
    height: calc(100vh - 64px);
    background-size: 522px, 999px;
    background-repeat: no-repeat;
    background-position: top left, right bottom;
    padding-top: 64px;
    display: flex;
    flex-direction: column;
}
.heroNaming .tagline {
    padding: 40px 20px;
    margin-left: auto;
}
.breadCrumbs {
    margin-top: auto;
    margin-bottom: 10px;
    text-align: left;
}
.breadCrumb {
    display: flex;
    gap: 13px;
}
.breadCrumb span {
    color: #000;
    font-family: "Noto Sans";
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 120%;
    letter-spacing: 1px;
    text-transform: uppercase;
}
.hastag {
    color: var(--text-black);
    font-family: "Noto Sans";
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}

/* Top Section ========================================== */
.topSection {
    position: relative;
    padding-bottom: 646px;
}
.topSection .contentWrapper {
    display: flex;
    flex-direction: column;
    gap: 310px;
    width: inherit;
}
.topSection .textContent {
    width: 50%;
    padding-top: 100px;
    max-width: 638px;
    margin-left: auto;
    text-align: left;
    display: flex;
    flex-direction: column;
    gap: 75px;
}
.namingParagraph {
    font-family: "Noto Sans";
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}
.namingParagraph.capitalized::first-letter {
    color: #000;
    font-family: "Noe Display";
    font-size: 100px;
    font-style: normal;
    font-weight: 500;
    float: left;
    margin-top: 25px;
    line-height: 50%;
}
.namingTitle40px {
    font-family: "Noe Display";
    font-size: 40px;
    font-style: normal;
    font-weight: 500;
    line-height: 100%;
}
.namingQuote {
    padding-left: 35px;
    position: relative;
}
.namingQuote::before {
    content: "";
    width: 7px;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: var(--text-black);
}
.namingQuote li {
    color: var(--text-black);
    font-family: "Noe Display";
    font-size: 30px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}
.topSection .floatingImage {
    position: absolute;
    height: auto;
    visibility: visible;
    left:0px;
    transition: all 1000ms cubic-bezier(0.670, 0.005, 0.290, 0.995);
}
.topSection .floatingImage.verizonPhone {
    width: 603px;
    top: 110px;
    left: -95px;
    z-index: 1;
}
.topSection .floatingImage.offJhonson {
    width: 560px;
    top: 536px;
    left: -163px;
    z-index: 2;
}
.topSection .floatingImage.duracell {
    width: 376px;
    top: 1300px;
    left: -42px;
    z-index: 3;
}
.topSection .floatingImage.sprite {
    width: 595px;
    top: 1543px;
    left: -150px;
    z-index: 4;
}
.topSection .floatingImage.robotott {
    width: 535px;
    top: 1916px;
    left: 120px;
    z-index: 5;
}
.topSection .floatingImage.dog3D {
    width: 301px;
    top: 2026px;
    left: 10px;
    z-index: 6;
}
.topSection .floatingImage.guitar3D {
    width: 336px;
    top: 2112px;
    left: -40px;
    z-index: 8;
}
.topSection .floatingImage.touros {
    width: 1103px;
    top: 2406px;
    left: 11px;
    z-index: 5;
}
.topSection .floatingImage.sphere {
    width: 581px;
    top: 2514px;
    left: 721px;
    z-index: 4;
}

/* Demo styles ---------------------------------- */
.topSection.showDemo .floatingImage:not(.demoImage){
    left:-1000px;
    visibility: hidden;
}
.topSection .floatingImage.demoImage {
    width: 526px;
    top: 2184px;
    left: 90px;
    z-index: 7;
}
.topSection.showDemo .floatingImage.demoImage {
    transform: rotate(5deg) scale(1.2) translateY(-60px);
}
.topSection .interactionContent {
    width: 100%;
    max-width: 638px;
    margin-left: auto;
    text-align: left;
    display: flex;
    flex-direction: column;
    position: relative;
    z-index: 9999;
    transition: all 1000ms cubic-bezier(0.670, 0.005, 0.290, 0.995);
}
.topSection.showDemo .interactionContent {
    margin-top: 144px;
}
.interactionContent h3 {
    color: var(--text-black);
    font-family: "Noe Display";
    font-size: 200px;
    font-style: normal;
    font-weight: 500;
    line-height: 75%;
    text-transform: uppercase;
    margin: 0;
}
.interactionContent h3:nth-of-type(2) {
    padding-left: 115px;
}
.interactionContent .gradientButton {
    width: 250px;
    padding: 24px 0;
}

.topSection .sourcing{    
    background:#202020;
    padding:10px;
    color:white;
    text-align: center;
    font-family: B612 Mono;
    font-size: 37px;
    font-weight: 400;
    line-height: 45px;
    letter-spacing: 0em;
    z-index: 8;
    position: absolute;
    width: 100%;
    left: 0px;
    top:calc(50% - 100px);    
}

.topSection .hideDemo{
    cursor: pointer;
    position: fixed;
    bottom:50px;
    left:50px;
    z-index: 999999;
}

.topSection .demo-content{
    height:100vh;
    position: relative;
    z-index: 9999;
    display: flex;
}
.mt-64px {
    margin-top: 64px;
}

.topSection .demo-content h2{
    font-family: Noe Display;
    font-size: 60px;
    font-weight: 500;
    line-height: 110%;
    letter-spacing: 0em;
    text-align: left;
    margin: 0px;
    max-width: 430px;
}

.topSection .demo-content h4{
    font-family: Noe Display;
    font-size: 40px;
    font-weight: 500;
    line-height: 110%;
    letter-spacing: 0em;
    text-align: left;
    margin: 0;
    max-width: 510px;
}

.topSection .demo-content p{
    font-family: Noto Sans;
    font-size: 20px;
    font-weight: 400;
    line-height: 27px;
    letter-spacing: 0em;
    text-align: left;
    margin-top:0px;
}

.topSection .demo-content .buttons{
    display: flex;
    flex-wrap: wrap;
    gap: 50px;
    margin-top: 60px;
}


.topSection .demo-content .result-demo{
    overflow: hidden;
    position: fixed;
    height: 100%;
    max-height: 682px;
    width: 100%;
    max-width: 966px;
    z-index: 99999;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.topSection .demo-content .result-demo h3{
    font-family: Noe Display;
    font-size: 60px;
    font-weight: 570;
    line-height: 120%;
    letter-spacing: 0em;
    text-align: left;
    background:white;
    max-width: 500px;
    padding:15px;
    margin: 25px 25px 10px;
}

.topSection .demo-content .result-demo p{
    font-family: Noto Sans;
    font-size: 16px;
    font-weight: 570;
    line-height: 22px;
    letter-spacing: 0em;
    text-align: left;
    background:white;
    margin-top:5px;
    max-width: 500px;
    padding:15px;
    margin:0 25px 25px;
}

.topSection .demo-content .gradientButton.tryAnotherButton{    
    z-index: 999990;
    position: fixed;
    right: 57px;
    bottom: 125px;
}


/* Meet firmi ================================= */
.meetFirmi {
    width: 100%;
    position: relative;
    background-color: var(--Lippincott-Gray-8);
    color: white;
}
.firmiWrapper {
    display: flex;
    flex-wrap: wrap;
}
.firmiColumnOne {
    flex: 0 0 auto;
    width: 43%;
    text-align: right;
    display: flex;
    justify-content: center;
    align-items: center;
}
.firmiColumnOne > div {
    padding: 0 72px;
}
.firmiColumnOne.second {
    align-items: flex-start;
}
.firmiColumnOne img {
    max-width: 100%;
    height: auto;
    width: 482px;
}
.firmiColumnOne h2 {
    font-family: "Noe Display";
    font-size: 60px;
    font-style: normal;
    font-weight: 500;
    line-height: 110%;
    text-align: left;
}
.firmiColumnTwo {
    flex: 0 0 auto;
    width: 57%;
    background-color: #000;
    text-align: left;
}
.paddingTextOne {
    padding-top: 250px;
    padding-bottom: 100px;
}
.twoTextWrap {
    max-width: 638px;
    padding-left: 110px;
}
.twoTextWrap h2 {
    font-family: "Noe Display";
    font-size: 100px;
    font-style: normal;
    font-weight: 500;
    line-height: 100%;
    margin: 0 0 100px;
}
.twoTextWrap p {
    font-family: "Noto Sans";
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0 0 75px;
}
.twoTextWrap h3 {
    font-family: "Noe Display";
    font-size: 40px;
    font-style: normal;
    font-weight: 500;
    line-height: 110%;
    margin: 0 0 75px;
}
.twoTextWrap ul {
    padding: revert;
    margin-bottom: 75px;
}
.twoTextWrap ul li {
    list-style: disc;
    font-family: "Noe Display";
    font-size: 30px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}
.twoTextWrap ul li + li {
    margin-top: 32px;
}
.twoTextWrap img {
    max-width: 100%;
    height: auto;
}
.firmiDivider {
    width: 90%;
    margin: 0 auto;
    height: 1px;
    background-color: white;
    margin-bottom: 50px;
}
.videoContainer {
    cursor: pointer;
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
}

.videoContainer video {
    width: 100%;
    height: auto;
}

