/* Defining colors and variables ================== */
:root {   
  --light-gray: #252525;
  --Lippincott-Gray-4: #ADADAD;
  --Lippincott-Gray-5: #8F8F8F;
  --Lippincott-Gray-7: #252525;
  --Lippincott-Gray-8: #202020;
  --Lippincott-Gray-10: #111;
  --Lippincott-Black: #191919;
  --Lippincott-Lavender-Medium: #BA9CFF;
}

/* Defining structure ============================= */
.dFlex { display: flex; }
.spaceBetween { justify-content: space-between; }
.alignItemsCenter { align-items: center; }
.alignItemsStart { align-items: flex-start; }
.flexColumn { flex-direction: column; }

.robotsSection {
  background: linear-gradient(116deg, #E2FFD4 6.06%, #E9FFFA 38.98%, #FFF2D0 71.89%, #FFE6DE 104.81%);
  background-blend-mode: multiply;
  position: sticky;
  overflow: hidden;
}

.robotsSection p,
.robotsSection ul li {
  color: #000;
  font-family: "Noto Sans";
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.robotsSection ul li {
  list-style-type: disc;
}

.robotsWrapper {
  max-width: 1290px;
  margin: 0 auto;
  padding-left: 24px;
  padding-right: 24px;
  text-align: left;
}

.robotsWrapper > div {
  position: relative;
}

.robotsWrapper img {
  position: absolute;
  z-index: -1;
}

.robotHeading {
  color: #000;
  font-family: "Noe Display";
  font-size: 150px;
  font-style: normal;
  font-weight: 500;
  line-height: 84%;
  margin: 0;
}
.grayHeadings .robotHeading {
  color: #5f5f5f;
}

.robotBlockOne { padding: 130px 0 52px; }
.robotBlockOne h2:nth-of-type(1) { padding-left: 166px; }
.robotBlockOne h2:nth-of-type(3) { padding-left: 257px; }
.robotOne {
  top: -120px;
  right: 3%;
  width: 335px;
  position: absolute;
  /*transition: all 0.4s ease-out;*/
}

.robotBlockTwo {
  padding: 52px 0 38px;
  position: relative;
}
.robotBlockTwo h2:nth-of-type(2) { padding-left: 62px; }
.robotBlockTwo h2:nth-of-type(3) { padding-left: 216px; }
.robotBlockTwo .robotTextWrapper:nth-of-type(1) { 
  max-width: 636px;
  margin-bottom: 80px; 
}
.robotBlockTwo .robotTextWrapper:nth-of-type(2) {
  max-width: 590px;
  margin-top: 40px;
}

.robotTwo {
  position: absolute;
  top: 200px;
  right: -12%;
  width: 782px;
  z-index: 0;
  /*transition: all 0.4s ease-out;*/
}

.robotBlockThree {
  padding: 38px 0 0;
  position: relative;
}
.robotBlockThree .robotsWrapper {
  text-align: right;
}

.robotBlockThree .headingBlockOne h2:nth-of-type(1) { padding-right: 52px; }
.robotBlockThree .headingBlockOne h2:nth-of-type(3) { padding-right: 264px; }
.robotBlockThree .headingBlockTwo h2:nth-of-type(1) { padding-right: 158px; }
.robotBlockThree .headingBlockTwo h2:nth-of-type(2) { padding-right: 196px; }

.robotBlockThree .robotTextWrapper {
  max-width: 480px;
  margin-left: auto;
  text-align: left;
  margin-bottom: 150px;
}

.robotThree {
  position: absolute;
  top: 90px;
  left: -12%;
  width: 848px;
  z-index: 0;
  /*transition: all 0.4s ease-out;*/
}

.robotBlockFour {
  padding: 0 0 440px;
  text-align: right;
  position: relative;
}

.robotBlockFour .robotTextWrapper {
  max-width: 638px;
  margin-left: auto;
  text-align: left;
}

.robotBeach {
  position: absolute;
  left: -32%;
  bottom: 0;
  width: 2180px;
  z-index: 0;
  /*transition: all 0.4s ease-out;*/
}

/* Responsive ================================= */
@media (max-width: 1280px) {
  .robotsWrapper {
    padding-left: 32px;
    padding-right: 32px
  }
  .robotHeading {
    font-size: 130px;
  }
  .robotOne { width: 330px; }
  .robotTwo { width: 680px; }
  .robotThree { width: 760px; }
  .robotBeach { width: 1880px; }
}

@media (max-width: 1280px) {
  .robotHeading {
    font-size: 100px;
  }
  .robotOne { width: 250px; }
  .robotTwo { width: 420px; }
  .robotThree { width: 600px; }
  .robotBeach { width: 1580px; }
}

@media (max-width: 768px) {
  .negativeRow {
    margin-left: -32px;
    margin-right: -32px;
  }
  .robotHeading {
    font-size: 86px;
  }

  .robotsSection p, .robotsSection ul li {
    font-size: 18px;
  }

  .robotBlockOne h2:nth-of-type(1) { padding-left: 80px; }
  .robotBlockOne h2:nth-of-type(3) { padding-left: 120px; }
  .robotOne { width: 200px; }

  .robotBlockTwo h2:nth-of-type(1) { padding-left: 60px; }
  .robotBlockTwo h2:nth-of-type(2) { padding-left: 0px; }
  .robotBlockTwo h2:nth-of-type(3) { padding-left: 180px; }

  .robotTwo{
    width: 360px;
    top: auto;
    bottom: 0;
  }

  .robotBlockTwo{
    padding-bottom:380px;
  }

  .robotBlockThree .robotsWrapper {
    text-align: left;
  }
  .robotBlockThree .headingBlockOne h2:nth-of-type(1),
  .robotBlockThree .headingBlockTwo h2:nth-of-type(1) {
    padding-right: 0;
    padding-left: 80px;
  }
  .robotBlockThree .headingBlockOne h2:nth-of-type(2),
  .robotBlockThree .headingBlockTwo h2:nth-of-type(2) {
    padding-right: 0;
  }
  .robotBlockThree .headingBlockOne h2:nth-of-type(3),
  .robotBlockThree .headingBlockTwo h2:nth-of-type(3) {
    padding-right: 0;
    padding-left: 180px;
  }

  .robotBlockThree .robotTextWrapper{
    padding-bottom: 460px;
  }

  .robotThree{
    width: 480px;
    top: 520px;
  }

  .robotBlockFour {
    padding: 0 0 370px;
  }
  .robotBlockFour .robotTextWrapper {
    margin-left: 0;
  }
  .robotBeach {
    left: -46%;
    width: 1260px;
  }

}

@media all and (max-width: 480px) {  
  .robotHeading {
    font-size: 60px;
  }
  .robotsSection p, .robotsSection ul li { font-size: 16px; }
  .robotsSection ul { padding-inline-start: 32px; }

  .robotOne { width: 170px; }
  .robotBlockOne{ padding-top: 360px; }
  .robotBlockOne h2:nth-of-type(1) { padding-left: 37px; }
  .robotBlockOne h2:nth-of-type(3) { padding-left: 90px; }

  .robotTwo { width: 410px; }
  .robotBlockTwo { padding-bottom: 440px; }
  .robotBlockTwo h2:nth-of-type(1) { padding-left: 54px; }
  .robotBlockTwo h2:nth-of-type(3) { padding-left: 155px; }

  .robotBlockThree .headingBlockOne h2:nth-of-type(1),
  .robotBlockThree .headingBlockTwo h2:nth-of-type(1) {
    padding-left: 47px;
  }
  .robotBlockThree .headingBlockOne h2:nth-of-type(3),
  .robotBlockThree .headingBlockTwo h2:nth-of-type(3) {
    padding-left: 107px;
  }

  .robotThree { top: 400px; }

  .robotBeach {
    left: -76%;
    width: 1180px;
  }

}