/* KeepExploring ======================= */
.keepExploring {
    background-color: var(--Lippincott-Gray-8);
    padding: 100px 0 90px;
}

.exploringCard {
    margin-top: 48px;
    padding: 337px 55px 67px;
    border-radius: 20px;
    text-align: left;
    background-size: cover; 
    background-position: center; 
    position: relative;
}

.exploringCard::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: linear-gradient(82deg, rgba(0, 0, 0, 0.20) 12.48%, rgba(0, 0, 0, 0.00) 94%);
    z-index: 1;
}

.exploringCard h4 {
    position: relative;
    max-width: 534px;
    z-index: 10;
}

@media screen and (max-width: 1024px) {
    .exploringCard {
        padding: 330px 32px 32px;
    }
}

@media screen and (max-width: 480px) {
    .exploringCard {
        padding-top: 200px;
    }
}

@media screen and (max-width: 400px) {
    .exploringCard {
        padding: 200px 24px 24px;
    }
}